nav {
    position: relative;
    left: 0;
    right: 0;
    top: 0;
    height: 70px;
    width: 100%;
    background-color: black;
    position:sticky;
    z-index: 3;
}

nav ul {
    list-style-type: none;
    overflow: hidden;
    margin: 0;
}

nav li {
    float: left;
}  

nav li a {
    display: block;
    text-align: center;
    color: white;
    padding: 1rem;
    text-decoration: none;
    font-size: 150%;
}

@media only screen and (max-width: 600px) {
    .navList {
        display: none;
    }
  }

@media only screen and (min-width: 600px) {
    .navListSmall {
        display: none;
    }
  }